@import './theme/vars.css';

.hidden {
  display: none;
}

.visible {
  font-family: 'Inter', sans-serif;
  display: initial;
  width: 40px;
  height: 40px;
  position: fixed;
  bottom: 32px;
  right: 48px;
  z-index: 2147483646;
  background-color: var(--color-white);
  box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px #00000014,
    0px 3px 6px -4px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 250ms;
  transition-property: width, right;
}

.visible img {
  all: initial;
}

.visible svg {
  all: initial;
}

.visibleSlideIn {
  font-family: 'Inter', sans-serif;
  display: initial;
  width: 140px;
  height: 40px;
  position: fixed;
  bottom: 32px;
  right: 16px;
  z-index: 2147483646;
  background-color: var(--color-white);
  box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px #00000014,
    0px 3px 6px -4px rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: right 400ms;
}

.visibleSlideIn img {
  all: initial !important;
}

.visibleSlideIn svg {
  all: initial !important;
}

.visibleSlideInNoSaveTime {
  width: 130px;
  padding-left: 6px;
}

.outRight {
  right: -60px;
}

.visibleExpanded {
  width: 206px;
  right: 16px;
}

.visibleExpandedLong {
  width: 218px;
  right: 16px;
}

.visibleDisplayingSavedTime {
  transition: width 350ms;
  width: 140px;
}

.visibleDisplayingSavedTime3Digits {
  transition: width 350ms;
  width: 150px;
}

.visibleDisplayingNoSavedTime {
  transition: width 350ms;
  width: 130px;
  padding-left: 6px;
}

.separator {
  border: 1px solid #eeeeee;
  height: 20px;
  margin: 0px 4px;
}

.dragIndicator {
  font-size: 16px;
  all: initial;
  color: var(--color-black);
  min-width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.collapsed {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.collapsed svg {
  all: initial;
}

.savedTimeTag {
  position: absolute;
  top: -7px;
  right: -8px;
  background-color: var(--color-black);
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  height: 14px;
  visibility: hidden;
  width: 0px;
  transition: width 300s;
}

.savedTimeTagVisible {
  visibility: visible;
  width: auto;
  padding: 1px 3px 1px 3px;
}

.savedTimeLabel {
  all: initial;
  color: var(--color-white) !important;
  font-size: 12px !important;
  line-height: 14px !important;
  font-weight: 600 !important;
  margin-left: 1px !important;
  font-family: 'Inter', sans-serif;
}

.savedTimeIcon {
  width: 12px !important;
  height: 12px !important;
}

.expanded {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 4px;
}

.leftExpandedContainer {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.closeButtonContainer {
  margin-top: 6px;
}

.requestButton {
  all: initial;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  cursor: pointer;
}
