@import '../../theme/vars.css';

.container {
  background: #3d4c4b;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  width: 432px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
  position: absolute;
  bottom: 54px;
  align-self: center;
  justify-self: center;
}

.closeButton {
  position: absolute;
  right: 8px;
  top: 4px;
}

.label {
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-white);
  margin-left: 8px;
}
