.container {
  display: flex;
}

.button {
  all: initial;
  border: 0;
  padding: 0;
  margin: 0;
  background: none;
  z-index: 2147483647;
  width: 32px;
  height: 32px;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.button > svg:hover {
  cursor: pointer !important;
}

.button:hover {
  background-color: #f0f0f0 !important;
}

.button:active {
  background-color: #d9d9d9 !important;
}

.tooltip {
  top: 43px !important;
  right: 68px !important;
  position: absolute !important;
  z-index: 2147483647 !important;
  cursor: pointer !important;
}
