@import '../../theme/vars.css';

.container {
  box-sizing: border-box;
  padding: 16px 24px 6px;
  font-family: 'IBM Plex Sans', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  overflow-y: auto;
  max-height: 600px;
  width: 100%;
  height: 100%;
  min-height: 384px;
}

.summaryLabel {
  all: initial;
  font-weight: bold !important;
  font-size: 14px !important;
  line-height: 22px !important;
  color: var(--color-gray-3) !important;
  font-family: 'IBM Plex Sans', sans-serif !important;
}

.savedTimeLabel {
  all: initial;
  font-weight: bold !important;
  font-size: 14px !important;
  line-height: 22px !important;
  color: var(--color-brand-green);
  font-family: 'IBM Plex Sans', sans-serif !important;
}

.title {
  all: initial;
  font-family: 'IBM Plex Sans', sans-serif !important;
  font-weight: bold !important;
  font-size: 24px !important;
  line-height: 32px !important;
  color: var(--color-black) !important;
  margin: 0 !important;
  padding: 0 !important;
}

.container h2 {
  all: initial;
  font-family: 'IBM Plex Sans', sans-serif !important;
  font-weight: bold !important;
  font-size: 24px !important;
  line-height: 32px !important;
  color: var(--color-black) !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: 100% !important;
  overflow-wrap: break-word !important;
}

.separator {
  background: var(--color-divider) !important;
  min-height: 1px !important;
  width: 100% !important;
  margin: 16px 0 0 !important;
}

.content {
  font-family: 'Inter', sans-serif !important;
  font-size: 16px !important;
  line-height: 24px !important;
  align-items: center;
  display: flex;
  flex-direction: column;
  color: var(--color-black) !important;
}

.editor {
  font-family: 'Inter', sans-serif !important;
  font-size: 16px !important;
  line-height: 24px !important;
  align-items: center;
  display: flex;
  flex-direction: column;
  color: var(--color-black) !important;
}

.topContent {
  flex-grow: 1;
}

.extraPaddingTop {
  padding-top: 16px;
}

.bottomContent {
  display: flex;
  justify-content: center;
}

.editor ul {
  all: initial;
  margin: 8px 0 1px 0px !important;
  font-family: 'Inter', sans-serif !important;
  color: var(--color-black) !important;
}

.editor ul > li::marker {
  color: var(--color-black) !important;
}

.editor li {
  font-size: 16px !important;
  line-height: 24px !important;
  font-family: 'Inter', sans-serif !important;
  color: var(--color-black) !important;
  list-style-type: disc !important;
  margin-left: 20px !important;
  margin-bottom: 0px !important;
  margin-top: 0px !important;
}

.editor div > div > div > div > div {
  margin-bottom: 8px;
}

.editor span {
  all: initial;
  font-size: 16px !important;
  line-height: 24px !important;
  margin-bottom: 32px !important;
  font-family: 'Inter', sans-serif !important;
  color: var(--color-black) !important;
}

.editor h3 {
  all: initial;
}

.editor h3 div {
  margin: 16px 0 10px 0 !important;
}

.editor h3 span {
  font-weight: 600 !important;
  font-size: 18px !important;
  line-height: 24px !important;
  margin: 16px 0 10px 0 !important;
  font-family: 'Inter', sans-serif !important;
  color: var(--color-black) !important;
  padding: 0 !important;
}

.editor p {
  all: initial;
  font-size: 16px !important;
  line-height: 24px !important;
  margin-bottom: 32px !important;
  font-family: 'Inter', sans-serif !important;
  color: var(--color-black) !important;
}

.loadingContainer {
  align-self: center !important;
  justify-self: center !important;
  align-items: center !important;
  justify-content: center !important;
  flex: 1 !important;
  flex-grow: 1 !important;
  display: flex !important;
}

.copyright {
  all: initial;
  font-size: 14px !important;
  line-height: 22px !important;
  text-align: center !important;
  color: #8c8c8c !important;
  font-family: 'Inter', sans-serif !important;
}

.errorContainer {
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
  margin: auto !important;
  padding: 32px 0 44px 0 !important;
}

.errorContainer .title {
  all: initial;
  font-family: 'IBM Plex Sans', sans-serif !important;
  font-style: normal !important;
  font-weight: bold !important;
  font-size: 18px !important;
  line-height: 24px !important;
  text-align: center !important;
  color: #222222 !important;
  margin-top: 32px !important;
}

.errorContainer .subtitle {
  all: initial;
  font-family: 'IBM Plex Sans', sans-serif !important;
  font-size: 16px !important;
  line-height: 20px !important;
  text-align: center !important;
  color: #595959 !important;
  margin-top: 4px !important;
  max-width: 334px !important;
}

.errorContainer .retryContainer {
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
}

.errorContainer svg {
  fill: none !important;
}

.errorButton {
  all: initial;
  margin-top: 32px !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  width: 200px !important;
  height: 48px !important;
  border: 2px solid #36db93 !important;
  border-radius: 8px !important;
  cursor: pointer !important;
  box-sizing: border-box !important;
}

.tryAgainButton {
  all: initial;
  margin-top: 32px !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  width: 200px !important;
  height: 48px !important;
  background: #36db93 !important;
  border-radius: 8px !important;
  cursor: pointer !important;
}

.errorButtonLabel {
  all: initial;
  font-family: 'Inter', sans-serif !important;
  font-style: normal !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  line-height: 24px !important;
  cursor: pointer;
}

.support {
  all: initial;
  display: flex !important;
  flex-direction: row !important;
  align-items: center !important;
  justify-content: center !important;
  margin-top: 24px !important;
  font-family: 'Inter', sans-serif !important;
  font-style: normal !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  line-height: 24px !important;
  color: #1da1f2;
}

.support img {
  all: initial !important;
  width: 20px !important;
  height: 14px !important;
  margin-right: 5px !important;
}

.support:hover {
  all: initial;
  display: flex !important;
  flex-direction: row !important;
  align-items: center !important;
  justify-content: space-between !important;
  margin-top: 24px !important;
  font-family: 'Inter', sans-serif !important;
  font-style: normal !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  line-height: 24px !important;
  color: #1da1f2;
  cursor: pointer;
}

.support:active {
  all: initial;
  display: flex !important;
  flex-direction: row !important;
  align-items: center !important;
  justify-content: space-between !important;
  margin-top: 24px !important;
  font-family: 'Inter', sans-serif !important;
  font-style: normal !important;
  font-weight: 600 !important;
  font-size: 16px !important;
  line-height: 24px !important;
  color: #1da1f2;
}

.firewallDisclaimer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 16px;
  padding: 16px 24px;
  background: #ffede9;
  width: 480px;
  margin-left: -24px;
  box-sizing: border-box;
}

.firewallDisclaimer svg {
  width: 33px !important;
  height: 20px !important;
  margin-right: 14px !important;
}

.firewallDisclaimer p {
  all: auto;
  font-family: 'Inter', sans-serif !important;
  font-size: 14px !important;
  line-height: 20px !important;
  color: #444748 !important;
  margin: 0 !important;
}
