@import '../../theme/vars.css';

.container {
  font-family: 'Inter', sans-serif;
  box-sizing: border-box;
  min-width: 480px;
  max-width: 480px;
  min-height: 350px;
  max-height: 96%;
  position: fixed;
  display: flex;
  flex-direction: column;
  top: 32px;
  right: 16px;
  background-color: var(--color-white);
  z-index: 2147483646;
  box-shadow: -3px 9px 28px 8px rgba(0, 0, 0, 0.05),
    -3px 6px 16px rgba(0, 0, 0, 0.08), -1px 3px 6px -4px rgba(0, 0, 0, 0.12);
  border-radius: 20px;
  transition: right 0.4s;
}

.container img {
  all: initial;
  filter: initial !important;
  -webkit-filter: initial !important;
}

.container svg {
  all: initial;
  filter: initial !important;
  -webkit-filter: initial !important;
}

.outRight {
  right: -500px;
}

.topBar {
  box-sizing: border-box;
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  background-color: var(--color-gray-2);
  border-bottom: 1px solid var(--color-divider);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.topBarRightSection {
  display: flex;
  align-items: center;
  flex-direction: row;
  top: 8px;
}

.topBarDivider {
  height: 20px;
  border: 1px solid var(--color-divider);
  margin: 0 20px;
}

.joggoIcon {
  all: initial;
  width: 32px;
  height: 32px;
  cursor: pointer;
  border-radius: 8px;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}

.joggoIcon > svg:hover {
  cursor: pointer !important;
}

.joggoIcon:hover {
  background-color: #f0f0f0;
}

.joggoIcon:active {
  background-color: #d9d9d9;
}

.contentContainer {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  min-height: 384px;
  max-height: 784px;
  overflow-y: scroll;
}

.footer {
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-gray-2);
  border-top: 1px solid var(--color-divider);
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
